import { Directive, ElementRef, HostListener } from '@angular/core';
declare var $: any;
declare var mixitup: any;
@Directive({
  selector: '[appMixitup]'
})
export class MixitupDirective {

  constructor(private el: ElementRef) { }
  @HostListener('window:scroll') novingOn() {
$(function() {
      const mixer = mixitup($('#port'));
  });
  }

}
